<template>
  <v-app>
    <v-img :src="profile.store_data.file_01" cover max-height="200"></v-img>

    <v-container class="pa-0 ma-0" fluid>
      <!-- MODAL -->
      <div>
        <v-dialog v-model="overlay">
          <div class="sample-modal">
            <iframe
              class="sample_iframe"
              :src="clicked_item.review_url"
            ></iframe>
          </div>
        </v-dialog>
      </div>
      <!-- MODAL -->
      <!----------------------------------------------- Divider -->
      <v-row class="justify-center">
        <div class="sample-dot"></div>
      </v-row>
      <!----------------------------------------------- Tabs -->
      <v-row class="sample-tabs">
        <v-divider></v-divider>
        <v-tabs
          next-icon="mdi-arrow-right-bold-box-outline"
          prev-icon="mdi-arrow-left-bold-box-outline"
          size="small"
          slider-color="orange"
          background-color="transparent"
          color="orange-accent-4"
          show-arrows
        >
          <v-tab
            size="small"
            @click="category_fn_all()"
            v-show="this.profile.user_profile.product_category == null"
            >all</v-tab
          >
          <v-tab
            v-for="item in product_category"
            size="small"
            :key="item"
            :value="item.id"
            @click="category_fn(item)"
            >{{ item.category }}
          </v-tab>
        </v-tabs>
        <v-divider></v-divider>
      </v-row>
      <!----------------------------------------------- Tabs -->
      <v-row class="d-flex sample-rowbox">
        <v-col
          class="sample-colbox"
          v-for="it in category_content"
          :key="it"
          cols="12"
          md="3"
          sm="4"
        >
          <v-card>
            <v-img height="200" :src="it.image_url" cover>
              <div class="d-flex fill-height justify-center align-center">
                <v-btn
                  prepend-icon="mdi-play"
                  color="white"
                  variant="outlined"
                  @click="
                    overlay = true;
                    clicked_item_fn(it);
                  "
                >
                  Preview</v-btn
                >
                <div class="text-center"></div>
              </div>
            </v-img>
            <v-card-title class="sample-content"
              ><v-icon color="orange-lighten-2" size="small"
                >mdi-filmstrip</v-icon
              >
              {{ it.product_type }}
            </v-card-title>
            <v-card-text class="sample-content">
              {{ it.content_text }}
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions class="justify-end">
              <span
                class="font-weight-light text-caption white text-grey"
                color="grey"
                >{{ it.guide_text }}</span
              >
              <v-spacer></v-spacer>
              <v-btn
                color="orange"
                variant="outlined"
                @click="product_select(it)"
                >Select</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import http from "@/api/http";

export default {
  name: "SaMples",
  data() {
    return {
      overlay: false,
      product_category: null,
      all_content: {},
      category_content: [],
      confirm_ox: null,
      clicked_item: null,
    };
  },
  computed: {
    ...mapState(["user", "content", "profile"]),
  },
  mounted() {
    http.getProductTy().then((resp) => {
      this.all_content = resp.data;
      this.category_content = this.all_content;
    });

    if (this.profile.user_profile.product_category == undefined) {
      http.getCategory(0).then((resp) => {
        this.product_category = resp.data;
      });
    } else {
      http
        .getCategory(this.profile.user_profile.product_category)
        .then((resp) => {
          this.product_category = resp.data;
          this.category_fn(this.product_category[0]);
        });
    }
  },
  methods: {
    ...mapMutations(["remove_user_status"]),
    ...mapActions(["user_verify"]),

    category_fn_all() {
      this.category_content = this.all_content;
    },
    category_fn(item) {
      let temp = [];
      for (let content of this.all_content) {
        if (item.id == content.product_category) {
          temp.push(content);
        }
      }
      this.category_content = temp;
    },
    product_select(item) {
      if (this.profile.user_profile.product_type) {
        this.confirm_ox = confirm(
          "고객님께서는 이미 " +
            this.profile.product_type.product_type +
            " 선택 하셨습니다. 바꾸시면 기존 내용이 지워집니다."
        );
      } else if (this.profile.user_profile.product_type == null) {
        let type_number;
        for (let temp of this.all_content) {
          if (temp.id == item.id) {
            type_number = temp;
          }
        }
        this.confirm_ox = confirm(
          type_number.product_type + "를 선택 하시겠습니까?"
        );
      }

      if (this.confirm_ox) {
        // location.reload();
        this.profile.user_profile.product_type = item.id;
        this.profile.user_profile.process_step = 3;


        this.$store.dispatch("profile/setProfile", {
          id: this.profile.user_profile.id,
          data: this.profile.user_profile,
        });
      }
    },
    clicked_item_fn(item) {
      this.clicked_item = item;
    },
  },
};
</script>

<style>
.sample_iframe {
  display: block;
  border: none;
  height: 40vh;
  width: 80vw;
}
.sample-modal {
  height: 100%;
  width: 100%;
  background: transparent;
}
.sample-tabs {
  margin: 30px 50px 10px 50px;
  justify-content: center;
}
.sample-rowbox {
  margin: 0px;
  padding: 0px;
  justify-content: start;
}
.sample-colbox {
  padding: 10px 30px;
}

.sample-dot {
  width: 20px;
  height: 20px;
  margin: 50px 0px 20px 0px;
  border-radius: 100%;
  background-color: orange;
  opacity: 100%;
  z-index: 9999;
}
.sample-content {
  text-align: start;
}
</style>